html {
  font-size: 4px;
}
/* 320: Iphone 5/SE */
/* 375: iPhone 13 mini, iPhone 12 mini, iPhone 11 Pro, iPhone Xs, and iPhone X */
@media only screen and (min-width: 320px) {
  html {
    font-size: 5px;
  }
}
/* 390: iPhone 13, iPhone 12 and iPhone 12 Pro, iPhone 14, iPhone 14 Pro */
/* 414: iPhone 11, iPhone XR, iPhone 11 Pro Max and iPhone Xs Max */
/* 428: iPhone 13 Pro Max and iPhone 12 Pro Max, iPhone 14 Pro Max */
@media only screen and (min-width: 390px) {
  html {
    font-size: 6px;
  }
}
/* iPads */
@media only screen and (min-width: 768px) {
  html {
    font-size: 7px;
  }
}
/* small desktop */
@media only screen and (min-width: 1024px) {
  html {
    font-size: 8px;
  }
}
/* HD */
@media only screen and (min-width: 1280px) {
  html {
    font-size: 9px;
  }
}
/* FHD */
@media only screen and (min-width: 1920px) {
  html {
    font-size: 10px;
  }
}
/* 2K */
@media only screen and (min-width: 2560px) {
  html {
    font-size: 11px;
  }
}
/* 4K */
@media only screen and (min-width: 3840px) {
  html {
    font-size: 12px;
  }
}
