/* width */
::-webkit-scrollbar {
  width: 2px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@font-face {
  font-family: Atomosfer;
  src: url(./assets/fonts/Atomosfer.ttf);
}
@font-face {
  font-family: JosefinSans;
  src: url(./assets/fonts/JosefinSans.ttf);
}
@font-face {
  font-family: JosefinSansLight;
  src: url(./assets/fonts/JosefinSans-Light.ttf);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}
